import React from 'react';
import { graphql, Link } from 'gatsby';

import Layout from '../components/layout';
import SEO from '../components/seo';

import { getLocalizedPath } from '../utils';

import suscribeIcon from '../images/icons/subscribe.svg';
import ccIcon from '../images/icons/license/cc.svg';
import byIcon from '../images/icons/license/by.svg';
import saIcon from '../images/icons/license/sa.svg';

interface Props {
  pageContext: {
    id: string;
    locale: string;
    canonical: boolean;
    alternatePages: {
      slug: string;
      locale: string;
    }[];
  };
  data: {
    content: {
      html: string;
      frontmatter: {
        title: string;
        thumbnail: string;
      };
    };
    previous: {
      frontmatter: {
        title: string;
        thumbnail: string;
      };
      fields: {
        localizedSlug: string;
      };
    };
    next: {
      frontmatter: {
        title: string;
        thumbnail: string;
      };
      fields: {
        localizedSlug: string;
      };
    };
  };
}

const Worksystem = ({ pageContext, data }: Props) => {
  const { locale, canonical, alternatePages } = pageContext;
  const post = data.content;
  return (
    <>
      <SEO
        title={post.frontmatter.title}
        image={post.frontmatter.thumbnail}
        locale={locale}
        canonical={canonical}
        alternatePages={alternatePages}
      />
      <Layout locale={locale}>
        <div>
          <div className="container py-32 md:py-36 text-grey">
            <Link to={getLocalizedPath(locale, '/worksystems')}>
              <div className="flex">
                <img src={suscribeIcon} className="icon-white transform rotate-180 w-6" />
                <p className="px-2">Back to worksystems</p>
              </div>
            </Link>
            <div className="md:flex items-center justify-between">
              <h1 className="text-5xl pt-12 md:pt-24 sm:text-6xl md:text-9xl text-white">
                {post.frontmatter.title}
              </h1>
              <div className="py-6 md:py-0">
                <img src={post.frontmatter.thumbnail} className="h-24 md:h-36 icon-white mx-auto" />
              </div>
            </div>
            <div className="content" dangerouslySetInnerHTML={{ __html: post.html }} />
            <div className="flex justify-between pt-16">
              <div className="self-start">
                {data.previous && (
                  <Link to={data.previous.fields.localizedSlug}>
                    <p className="text-grey text-xs">Previous worksystem</p>
                    <div className="flex flex-col-reverse md:flex-row items-center">
                      <img src={suscribeIcon} className="icon-white transform rotate-180 w-6" />
                      <b className="md:px-2 text-sm md:text-xl">
                        {data.previous.frontmatter.title}
                      </b>
                    </div>
                  </Link>
                )}
              </div>
              <div className="self-end">
                {data.next && (
                  <Link to={data.next.fields.localizedSlug}>
                    <p className="text-grey text-xs text-right">Next worksystem</p>
                    <div className="flex flex-col md:flex-row items-center">
                      <b className="md:px-2 text-sm md:text-xl">{data.next.frontmatter.title}</b>
                      <img src={suscribeIcon} className="icon-white w-6" />
                    </div>
                  </Link>
                )}
              </div>
            </div>
          </div>
        </div>
      </Layout>
    </>
  );
};

export default Worksystem;

export const query = graphql`
  query ($id: String!, $locale: String!, $previousIndex: Int, $nextIndex: Int) {
    content: markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        title
        thumbnail
      }
    }
    previous: markdownRemark(
      fields: { locale: { eq: $locale } }
      frontmatter: { order: { eq: $previousIndex } }
    ) {
      frontmatter {
        title
        thumbnail
      }
      fields {
        localizedSlug
      }
    }
    next: markdownRemark(
      fields: { locale: { eq: $locale } }
      frontmatter: { order: { eq: $nextIndex } }
    ) {
      frontmatter {
        title
        thumbnail
      }
      fields {
        localizedSlug
      }
    }
  }
`;
